.header{display:flex;justify-content:center;align-items:center}.header__img{width:48px;height:48px}.header__title{margin-left:4px;font-size:3.0rem}

.home__description{margin-top:8px;color:white}.home__description:last-of-type{margin-bottom:8px}.home__description--inline{display:inline;padding-left:16px}.home__arrow{margin-right:8px}.home__slider{display:flex;align-items:center;grid-gap:8px;gap:8px;margin-top:8px}.home__to-hex{display:flex;align-items:center}.home__approximate-color-row{display:flex;justify-content:flex-start}.home__approximate_color-label{margin:16px 0}.home__complementary_color-label{margin:16px 0}

.approximate-color-item{height:20px;width:20px}

.base-color{width:100%;border:1px solid #030303}

.complementary-color-area{margin-top:16px;padding:16px 32px;border-radius:16px}.complementary-color-area__description{margin:4px 0}

.hex-text-field{display:inline-block}.hex-text-field__label{margin-right:4px;font-size:1.4rem}.hex-text-field__input{padding:4px 8px;width:96px;height:32px;border-radius:4px;background-color:#fff;color:#000;font-weight:normal}

.palette-label{display:flex;justify-content:center;align-items:center;width:32px;border:1px solid #030303;background-color:#d3d3d3;color:#000;-webkit-writing-mode:vertical-rl;-ms-writing-mode:tb-rl;writing-mode:vertical-rl}

.rgb-text-field__label{vertical-align:center}.rgb-text-field__input--red{width:48px;height:32px;margin-right:16px;padding:4px 8px;border-radius:4px;background-color:#fff;font-weight:normal;color:red}.rgb-text-field__input--green{width:48px;height:32px;margin-right:16px;padding:4px 8px;border-radius:4px;background-color:#fff;font-weight:normal;color:lime}.rgb-text-field__input--blue{width:48px;height:32px;margin-right:16px;padding:4px 8px;border-radius:4px;background-color:#fff;font-weight:normal;color:blue}

.slider--r{display:flex;justify-content:space-between;align-items:center;grid-gap:8px;gap:8px;width:160px;padding:8px 16px 8px 8px;border-radius:8px;box-shadow:2px 2px 8px #d3d3d3;color:#fff;background:linear-gradient(90deg, #000 0%, #5e0000 50%, red 100%)}.slider--g{display:flex;justify-content:space-between;align-items:center;grid-gap:8px;gap:8px;width:160px;padding:8px 16px 8px 8px;border-radius:8px;box-shadow:2px 2px 8px #d3d3d3;color:#fff;background:linear-gradient(90deg, #000 0%, #005e00 50%, lime 100%)}.slider--b{display:flex;justify-content:space-between;align-items:center;grid-gap:8px;gap:8px;width:160px;padding:8px 16px 8px 8px;border-radius:8px;box-shadow:2px 2px 8px #d3d3d3;color:#fff;background:linear-gradient(90deg, #000 0%, #00005e 50%, blue 100%)}.slider--h{display:flex;justify-content:space-between;align-items:center;grid-gap:8px;gap:8px;width:160px;padding:8px 16px 8px 8px;border-radius:8px;box-shadow:2px 2px 8px #d3d3d3;color:#fff;background:linear-gradient(90deg, red 0%, orange 14%, #ff0 28%, green 42%, cyan 56%, blue 70%, purple 84%, red 100%)}.slider--s{display:flex;justify-content:space-between;align-items:center;grid-gap:8px;gap:8px;width:160px;padding:8px 16px 8px 8px;border-radius:8px;box-shadow:2px 2px 8px #d3d3d3;color:#fff;background:linear-gradient(90deg, #fff 0%, #61dafb 100%);color:#000}.slider--v{display:flex;justify-content:space-between;align-items:center;grid-gap:8px;gap:8px;width:160px;padding:8px 16px 8px 8px;border-radius:8px;box-shadow:2px 2px 8px #d3d3d3;color:#fff;background:linear-gradient(90deg, #000 0%, #61dafb 100%)}

