@charset "utf-8";

@import "_variables.scss";

.hex-text-field {
    display: inline-block;

    &__label {
        margin-right: 4px;
        font-size: 1.4rem;
    }

    &__input {
        padding: 4px 8px;
        width: 96px;
        height: 32px;
        border-radius: 4px;
        background-color: $white;
        color: $black;
        font-weight: normal;
    }
}
