@charset "utf-8";

@import "_variables.scss";
@import "_mixin.scss";

.rgb-text-field {

    &__label {
        vertical-align: center;
    }

    &__input {
        &--red {
            @include rgb-text-field-base();
            color: $red;
        }

        &--green {
            @include rgb-text-field-base();
            color: $green;
        }

        &--blue {
            @include rgb-text-field-base();
            color: $blue;
        }
    }
}
