@charset "utf-8";

@import "_variables.scss";
@import "_mixin.scss";

.home {
    &__description {
        margin-top: 8px;
        color: white;

        &:last-of-type {
            margin-bottom: 8px;
        }

        &--inline {
            display: inline;
            padding-left: 16px;
        }
    }

    &__arrow {
        margin-right: 8px;
    }

    &__slider {
        display: flex;
        align-items: center;
        gap: 8px;
        margin-top: 8px;
    }

    &__to-hex {
        display: flex;
        align-items: center;
    }

    &__approximate-color {
        &-row {
            display: flex;
            justify-content: flex-start;

        }
    }

    &__approximate_color-label {
        margin: 16px 0;
    }

    &__complementary_color-label {
        margin: 16px 0;
    }
}

